import Vue from 'vue'
import Vuex from 'vuex'
// import * as actions from './actions'
import * as getters from './getters'


Vue.use(Vuex)

const actions = {
    SET_EIGEN_DATA (state, eigenData) {
        state.eigenData.length = 0;
        for (let i = 0, len = eigenData.length; i < len; i++) {
            state.eigenData[i] = eigenData[i];   
        }
        //state.eigenData = eigenData;
    },
    SET_CUR_STATUS ({commit}, status) {
        commit('SET_CUR_STATUS', status);
        // state.curStatus = status;
    }
};

// 
const state = {
    // 
    filename: '',
    progressOk: true,
    failReason: '',

    // 
    projectName: '',
    projectId:'',
    projectStatus: true,
    originalData: {},

    tips: '',
    tipsStatus: false,
    // 
    selection: '',

    // step: 0-  1- 2-
    step: 0,
    // status: -1- 0- 1- 2- 
    
    // 
    uploadProgress: {
        name: '1.',
        percent: '0%',
        status: 1,
        failReason: ''
    },

    // 
    portraitProgress: {
        name: '2.',
        percent: '0%',
        status: 0  
    },
    //  
    modelProgress: {

    },
    progressItems: [],
    modelProgressItems: [],
    eigenData: [],
    queryList: [],
    trainObj: {
        featureName: '--',
        featureNum: '--',
        featureListId: -1,
        targetFeatureId: '',
        targetId: '',
        targetName: '--',
        timeSerialFeatureId: '',
        varifyNum: 0,
        splitMethod: 1,
        // 20% 5%
        testRatio: 20
    },
    timeTypeList: [],
    allTrainFinished: false,
    allModelFinished: false,
    fLId: '',
    targetId: '',
    taskId: '',
    targetName: '',
    modelStatus: false,

    // 0: 1: 2:  3:
    curStatus: 0,
    initTrainObj () {
        return {
            featureName: '',
            featureNum: 0,
            featureListId: -1,
            targetFeatureId: '',
            targetId: '',
            targetName: '',
            timeSerialFeatureId: '',
            varifyNum: 0,
            crossValidFold: 0,
            // 
            splitMethod: 1,
            // 20% 5%
            testRatio: 20
        }
    },
    showTargetTips: false,
}

//  mutations
const mutations = {
    // 
    SET_FILE_NAME (state, name) {
        state.filename = name;
    },
    SET_PROGRESS_OK (state, isOK) {
        state.progressOk = isOK;
    },
    SET_FAILREASON (state, failReason) {
        state.failReason = failReason;
    },

    // 
    SET_PROJECT_NAME (state, projectName) {
        state.projectName = projectName;
    },
    SET_PROJECT_ID (state, projectId) {
        state.projectId = projectId;
    },
    SET_PROJECT_STATUS (state, projectStatus) {
        state.projectStatus = projectStatus;
    },
    SET_ORIGINAL_DATA (state, dataSet) {
        state.originalData = dataSet;
    },

    SET_TIPS (state, tips) {
        state.tips = tips;
    },
    SET_TIPS_STATUS (state, status) {
        state.tipsStatus = status;
    },
    // 
    SET_SELECTION (state, selection) {
        state.selection = selection;
    },

    SET_EIGEN_DATA (state, eigenData) {
        state.eigenData.length = 0;
        for (let i = 0, len = eigenData.length; i < len; i++) {
            state.eigenData[i] = eigenData[i];   
        }
        //state.eigenData = eigenData;
    },
    SET_QUERY_LIST (state, queryList) {
        state.queryList = queryList;
    },
    SET_TYPE_LIST (state, timeTypeList) {
        state.timeTypeList = timeTypeList;
    },
    SET_TRAIN_STATUS (state, status) {
        state.allTrainFinished = status;
    },

    // ids
    SET_FL_ID (state, id) {
        state.fLId = id;
    },
    SET_TARGET_ID (state, id) {
        state.targetId = id;
    },
    SET_TARGET_NAME (state, name) {
        state.targetName = name;
    },
    SET_TASK_ID (state, id) {
        state.taskId = id;
    },
    SET_MODEL_STATUS (state, status) {
        state.modelStatus = status;
    },
    SET_CUR_STATUS (state, status) {
        state.curStatus = status;
    },
    SET_ALLMODEL_STATUS (state, status) {
        state.allModelFinished = status;
    },
    SET_TRAIN_OBJ (state, obj) {
        state.trainObj = obj;
    },
    SET_TARGET_TIPS (state, status) {
        state.showTargetTips = status;
    }
}

//  store 
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


